@import url('https://fonts.googleapis.com/css2?family=Orbitron:wght@400;700&display=swap');

* {
  font-family: 'Orbitron', sans-serif;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  background: linear-gradient(135deg, #0f0c29, #302b63, #24243e);
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  padding: 2rem;
  color: #00ffcc;
}

.App {
  text-align: center;
}

h1 {
  color: #00ffcc;
  margin-bottom: 1rem;
  font-size: 2rem;
  text-transform: uppercase;
  letter-spacing: 0.1rem;
  text-shadow: 0 0 10px #00ffcc, 0 0 20px #00ffcc;
}

.TodoWrapper {
  background: linear-gradient(135deg, #2a2a72, #009ffd);
  margin-top: 3rem;
  padding: 2.5rem;
  border-radius: 12px;
  box-shadow: 0 15px 40px rgba(0, 0, 0, 0.5);
  width: 100%;
  max-width: 450px;
  border: 1px solid #00ffcc;
}

.TodoForm {
  width: 100%;
  margin-bottom: 2rem;
}

.todo-input {
  outline: none;
  background: #1a1a2e;
  border: 1px solid #00ffcc;
  padding: 0.75rem 1.5rem;
  margin-top: 1.5rem;
  width: 100%;
  border-radius: 8px;
  color: #00ffcc;
  font-size: 1rem;
  transition: border 0.3s ease, box-shadow 0.3s ease;
}

.todo-input::placeholder {
  color: #00ffcc80;
}

.todo-input:focus {
  border-color: #00ffcc;
  box-shadow: 0 0 10px #00ffcc;
}

/* Added space above the button */
.todo-btn {
  background: #00ffcc;
  color: #000;
  border: none;
  padding: 0.75rem 1.5rem;
  border-radius: 8px;
  cursor: pointer;
  width: 100%;
  font-size: 1.1rem;
  letter-spacing: 0.05rem;
  text-transform: uppercase;
  margin-top: 2rem; /* Added space above the button */
  transition: background-color 0.3s ease, transform 0.3s ease, box-shadow 0.3s ease;
}

.todo-btn:hover {
  background: #009ffd;
  box-shadow: 0 0 15px #00ffcc, 0 0 25px #00ffcc;
  transform: translateY(-10px); /* Spaceship lift-off effect on hover */
}

/* Spaceship take-off animation on hover */
.todo-btn:active {
  animation: spaceship-takeoff 0.5s ease-out forwards;
}

@keyframes spaceship-takeoff {
  0% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(-20px);
  }
  100% {
    transform: translateY(1000px); /* Button "flies" off-screen */
  }
}

.Todo {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: #00ffcc;
  color: #000;
  padding: 0.75rem 1rem;
  border-radius: 8px;
  margin-bottom: 1rem;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.3);
  transition: background-color 0.3s ease;
}

.Todo:hover {
  background-color: #009ffd;
}

.fa-trash {
  margin-left: 0.75rem;
  transition: color 0.3s ease;
}

.fa-trash:hover {
  color: #ff0066;
}

.incompleted {
  color: #000;
  cursor: pointer;
}

.completed {
  color: #333;
  text-decoration: line-through;
  cursor: pointer;
}

.edit-icon,
.delete-icon {
  cursor: pointer;
  transition: transform 0.3s ease;
}

.edit-icon:hover,
.delete-icon:hover {
  transform: scale(1.15);
  color: #00ffcc;
}
